<template>
  <div>
    <a-page-header
        style="border: 1px solid rgb(235, 237, 240)"
        :title="$t('samplePreparation')"
        :sub-title="process.projectname"
    >
      <template slot="extra">
        <a-upload
            name="file"
            v-if="isEnd"
            :action="uploadAllUrl"
            :multiple="true"
            :headers="allUploadHeader"
            :showUploadList="false"
            @change="handleUploadChange"
            :disabled="disabledUpload"
        >
          <a-button :disabled="disabledUpload">
            <a-icon type="upload"/>
            {{ $t("upload") }}
          </a-button>
        </a-upload>
        <a-button type="danger" @click="batchUnPass()" v-if="canPase" :disabled="disabledPass">
          {{ $t("unPass") }}
        </a-button>
        <a-button type="primary" @click="submitData('pass')"
                  v-if="canPase" :disabled="disabledPass">
          {{ $t("pass") }}
        </a-button>
        <a-popconfirm placement="topLeft"
                      :ok-text="$t('yes')"
                      :disabled="canComplete"
                      :cancel-text="$t('no')"
                      v-if="isEnd"
                      @confirm="deleteByIds">
          <template slot="title">
            <p>{{ $t("areyousureDelete") }}</p>
          </template>
          <a-button :disabled="canComplete" type="danger">
            {{ $t("delete") }}
          </a-button>
        </a-popconfirm>

        <a-button @click="subTaskInfo"
                  :disabled="canDivide" type="primary" v-if="isEnd">
          {{ $t("divide") }}
        </a-button>

        <a-button type="primary" @click="submitData('complete')"
                  v-if="isEnd"
                  :disabled="canComplete">
          {{ $t("submit") }}
        </a-button>
        <a-button type="primary" @click="submitData('real')"
                  v-if="canPase"
                  :disabled="cansubmit">
          {{ $t("libraryPreparation") }}
        </a-button>
        <a-button @click="submitData('tmp')" v-if="isEnd">
          {{ $t("tmpSave") }}
        </a-button>
        <a-button icon="download" @click="downLoad"
                  :disabled="this.selectedRowKeys.length == 0">
          {{ $t("outPut") }}
        </a-button>
        <a-upload
            name="file"
            accept=".xls,.xlsx"
            v-if="isEnd"
            :show-upload-list="false"
            :headers="{token:this.$cookies.get('token'), processId : this.process.id}"
            @change="handUploadChange"
            :action="this.$axios.defaults.baseURL + '/file/import/makeInput'"
        >
          <a-button>
            <a-icon type="upload"/>
            {{ $t("input") }}
          </a-button>
        </a-upload>
      </template>
      <a-row type="flex">
        <a-tooltip>
          <a-tag class="pointer" color="#87d068" @click="showSubTask('03')">
            {{ $t("allAllow") + "(" + createrName + ")" }}
          </a-tag>
          <a-tag class="pointer" color="#108ee9" @click="showSubTask('02')">
            {{ $t("notAllow") + "(" + createrName + ")" }}
          </a-tag>
          <template v-if="isEnd">
            <a-tag class="pointer" color="blue" v-for="sub in subs" :key="sub.id" @click="showSubTask(sub.id)">
              {{ sub.name }}
            </a-tag>
          </template>
          <a-tag class="pointer" color="#f50" @click="showSubTask('00')">
            {{ $t("init") + "(" + operatorName + ")" }}
          </a-tag>
          <template slot="title">
            {{ $t("process.tagListTip") }}
          </template>
          <a-icon type="question-circle" theme="twoTone"/>
        </a-tooltip>
      </a-row>
    </a-page-header>
    <a-table :columns="columns"
             :data-source="data" bordered
             :loading="tableLoad"
             :scroll="scroll"
             :row-selection="rowSelection"
             :pagination="false"
             size="middle">
      <!--      -->

      <template slot="transformSampleTitle">
        <a-tooltip>
          <template slot="title">
            {{ $t("process.transformTip") }}
          </template>
          {{ $t("transformSample") }}
          <a-icon type="question-circle" theme="twoTone"/>
        </a-tooltip>
      </template>

      <template slot="derivativeindexTitle">
        <a-tooltip>
          <template slot="title">
            {{ $t("process.derivativeindexTip") }}
          </template>
          {{ $t("derivativeindex") }}
          <a-icon type="question-circle" theme="twoTone"/>
        </a-tooltip>
      </template>

      <template slot="testDateTitle">
        <icon-font style="font-size: 20px" type="icon-bitian"/>
        {{ $t("testDate") }}
      </template>

      <template slot="concentrationTitle">
        <icon-font style="font-size: 20px" type="icon-bitian"/>
        {{ "(ng/ul)//l)" }}
      </template>

      <template slot="sampleVolumeTitle">
        <icon-font style="font-size: 20px" type="icon-bitian"/>
        {{ this.$t("sampleVolume") + "(ul)" }}
      </template>

      <template slot="totalNumberTitle">
        <icon-font style="font-size: 20px" type="icon-bitian"/>
        {{ "/ug/" }}
      </template>

      <template slot="cellLifeTitle">
        <icon-font style="font-size: 20px" type="icon-bitian"/>
        {{ $t("cellLife") }}
      </template>

      <template slot="checkResultTitle">
        <icon-font style="font-size: 20px" type="icon-bitian"/>
        {{ $t("checkResult") }}
      </template>

      <template slot="checkUserTitle">
        <icon-font style="font-size: 20px" type="icon-bitian"/>
        {{ $t("checkUser") }}
      </template>

      <template slot="reviewerTitle">
        <icon-font style="font-size: 20px" type="icon-bitian"/>
        {{ $t("reviewer") }}
      </template>

      <template slot="databaseTypeTitle">
        <icon-font style="font-size: 20px" type="icon-bitian"/>
        {{ $t("databaseType") }}
      </template>

      <template slot="operationTitle">
        <a-tooltip>
          <template slot="title">
            {{ $t("process.operationTip") }}
          </template>
          {{ $t("operation") }}
          <a-icon type="question-circle" theme="twoTone"/>
        </a-tooltip>
      </template>

      <!--      -->
      <div
          slot="filterDropdown"
          slot-scope="{ setSelectedKeys, selectedKeys, confirm, clearFilters, column }"
          style="padding: 8px"
      >
        <a-input
            v-ant-ref="c => (searchInput = c)"
            :value="selectedKeys[0]"
            style="width: 188px; margin-bottom: 8px; display: block;"
            @change="e => setSelectedKeys(e.target.value ? [e.target.value] : [])"
            @pressEnter="() => handleSearch(selectedKeys, confirm, column.dataIndex)"
        />
        <a-button
            type="primary"
            icon="search"
            size="small"
            style="width: 90px; margin-right: 8px"
            @click="() => handleSearch(selectedKeys, confirm, column.dataIndex)"
        >
          {{ $t("search") }}
        </a-button>
        <a-button size="small" style="width: 90px" @click="() => handleReset(clearFilters)">
          {{ $t("reset") }}
        </a-button>
      </div>
      <a-icon
          slot="filterIcon"
          slot-scope="filtered"
          type="search"
          :style="{ color: filtered ? '#108ee9' : undefined }"
      />
      <template
          v-for="col in this.columnNames"
          :slot="col"
          slot-scope="text, record,index"
      >
        <div :key="col">

          <!--  -->
          <a-select style="width: 100%" v-if="col == 'cellsort'"
                    v-model="record.cellsort"
                    :disabled="colIsDisabed(record,col) || isStop(record) || isDisabled(record)"
                    @change="e => handleChange(e.target.value, record.key, col)"
          >
            <a-select-option v-for="item in cellSortMethods" :key="item.key" :value="item.key">
              {{ item.val }}
            </a-select-option>
          </a-select>
          <!-- initSample -->
          <a-select style="width: 100%" v-else-if="col == 'initsample'"
                    v-model="record.initsample"
                    disabled
          >
            <a-select-option v-for="item in sampleInits" :key="item.key" :value="item.key">
              {{ item.val }}
            </a-select-option>
          </a-select>
          <!--  -->
          <a-select style="width: 100%" v-else-if="col == 'transform'"
                    v-model="record.transform"
                    :disabled="isStop(record) || !isTissu(record) || isDisabled(record)"
                    @change="sampleInitChange(record)"
          >
            <a-select-option v-for="item in transformSamples" :key="item.key" :value="item.key">
              {{ item.val }}
            </a-select-option>
          </a-select>
          <!--  -->
          <a-select style="width: 100%" v-else-if="col == 'checkresult'"
                    :disabled="isStop(record) || isDisabled(record)"
                    v-model="record.checkresult"
                    @change="e => handleChange(e.target.value, record.key, col)"
          >
            <a-select-option v-for="item in checkresults" :key="item.key" :value="item.key">
              {{ item.val }}
            </a-select-option>
          </a-select>
          <!--  -->
          <a-select style="width: 100%" v-else-if="col == 'checkuser'"
                    v-model="record.checkuser"
                    :disabled="isStop(record) || isDisabled(record)"
                    @change="e => handleChange(e.target.value, record.key, col)"
          >
            <a-select-option v-for="item in allUsers" :key="item.id" :value="item.id">
              {{ item.name }}
            </a-select-option>
          </a-select>
          <!--  -->
          <a-select style="width: 100%" v-else-if="col == 'reviewer'"
                    v-model="record.reviewer"
                    :disabled="isStop(record) || isDisabled(record)"
                    @change="e => handleChange(e.target.value, record.key, col)"
          >
            <a-select-option v-for="item in allUsers" :key="item.id" :value="item.id">
              {{ item.name }}
            </a-select-option>
          </a-select>
          <!--  -->
          <a-select style="width: 100%" v-else-if="col == 'extractmethod'"
                    v-model="record.extractmethod"
                    :disabled="isStop(record) || isDisabled(record)"
                    @change="e => handleChange(e.target.value, record.key, col)"
          >
            <a-select-option v-for="item in extractmethods" :key="item.key" :value="item.key">
              {{ item.val }}
            </a-select-option>
          </a-select>
          <!--  -->
          <a-date-picker v-else-if="col == 'testdate'"
                         format="YYYY-MM-DD"
                         :disabled="isStop(record) || isDisabled(record)"
                         @change="e => handleChange(e.target.value, record.key, col)"
                         v-model="record.testdate"/>
          <!--  -->
          <a-select style="width: 100%" v-else-if="col == 'databasetype'"
                    v-model="record.databasetype"
                    :disabled="isStop(record) || isDisabled(record)"
                    @change="e => handleChange(e.target.value, record.key, col)"
          >
            <a-select-option v-for="item in databaseTypes(record.initsample,record.transform)" :key="item.key"
                             :value="item.key">
              {{ item.val }}
            </a-select-option>
          </a-select>
          <!--  -->
          <a-select style="width: 100%" v-else-if="col == 'sequencingplatform'"
                    v-model="record.sequencingplatform"
                    :disabled="isStop(record) || isDisabled(record)"
                    @change="e => handleChange(e.target.value, record.key, col)"
          >
            <a-select-option v-for="item in seqPlants" :key="item.key" :value="item.key">
              {{ item.val }}
            </a-select-option>
          </a-select>
          <a-input-number
              v-else-if="col == 'samplevolume'"
              style="width: 100%"
              v-model="record.samplevolume"
              :disabled="isStop(record)|| isDisabled(record)"
              :min="0"
              @change="e => handleChange(e.target.value, record.key, col)"
          />

          <a-input v-else-if="col == 'concentration'"
                   :disabled="isStop(record)|| isDisabled(record)"
                   v-model="record.concentration"
          >
            <a-select slot="addonAfter" default-value="ng/ul"
                      v-model="record.concentrationunit"
                      :disabled="isStop(record)|| isDisabled(record)"
                      style="width:130px">
              <a-select-option value="ng/ul">
                ng/ul
              </a-select-option>
              <a-select-option value="/l">
                /l
              </a-select-option>
            </a-select>
          </a-input>

          <!--          <a-input-number-->
          <!--              v-else-if="col == 'concentration'"-->
          <!--              style="width: 100%"-->
          <!--              v-model="record.concentration"-->
          <!--              :disabled="isStop(record)|| isDisabled(record)"-->
          <!--              :min="0"-->
          <!--              @change="e => handleChange(e.target.value, record.key, col)"-->
          <!--          />-->
          <a-input-number
              v-else-if="col == 'totalnumber'"
              style="width: 100%"
              v-model="record.totalnumber"
              :disabled="isStop(record)|| isDisabled(record)"
              :min="0"
              @change="e => handleChange(e.target.value, record.key, col)"
          />
          <div v-else-if="col == 'upload'">
            <a-upload
                name="file"
                :multiple="true"
                :disabled="isStop(record)|| isDisabled(record)"
                :headers="uploadHeader(record)"
                :action="uploadUrl"
                :showUploadList="false"
                @change="handleUploadChange"
            >
              <a-button :disabled="isStop(record) || isDisabled(record)">
                <a-icon type="upload"/>
                {{ $t("upload") }}
              </a-button>
            </a-upload>
          </div>
          <a-input
              v-else-if="col != 'index'"
              style="width: 100%"
              :value="text"
              v-model="record[col]"
              :disabled="colIsDisabed(record,col) || isStop(record)|| isDisabled(record)"
              @change="e => handleChange(e.target.value, record.key, col)"
          />
          <template v-else>
            {{ showText(col, text, index) }}
          </template>
        </div>
      </template>
      <template slot="operation" slot-scope="text, record">
        <div class="editable-row-operations">
          <span>
            <a-icon type="check-circle" theme="twoTone" two-tone-color="#52c41a" v-if="record.currentstatu == '03'"/>
            <a-icon type="clock-circle" theme="twoTone" two-tone-color="#FFCC00" v-if="record.currentstatu == '02'"/>
            <a-icon type="close-circle" theme="twoTone" two-tone-color="#eb2f96" v-if="record.currentstatu == '07'"/>
            <a-icon type="stop" theme="twoTone" two-tone-color="#CC0000" v-if="record.currentstatu == '09'"/>
            &nbsp;
          </span>
          <span v-if="record.currentstatu == '03'">
            &nbsp;
            <a-badge :count="record.libNum">
              <a-popconfirm
                  :title="$t('suretosubmit')"
                  @confirm="submitItem(record,'real')"
                  :disabled="!canPase"
                  :okText="$t('yes')"
                  :cancelText="$t('no')"
              >
                <a :disabled="!canPase">{{ $t("libraryPreparation") }}</a>
              </a-popconfirm>
            </a-badge>
            &nbsp;
          </span>
          <span v-if="record.currentstatu == '02'">
            &nbsp;
            <a @click="passItem(record,true)" :disabled="!canPase">{{ $t("pass") }}</a>
            &nbsp;
            <a @click="passItem(record,false)" :disabled="!canPase">{{ $t("unPass") }}</a>
            &nbsp;
          </span>
          <span>
            &nbsp;
            <a @click="showReason(record.id)" v-if="isStop(record)">{{ $t("showReason") }}</a>
            &nbsp;
            <a @click="showStopAlert(record.id)" v-if="!isStop(record) && !isDisabled(record)">{{ $t("stop") }}</a>
            &nbsp;
            <a-badge
                :count="showFileCount(record.id)"
                :number-style="{
                  backgroundColor: '#fff',
                  color: '#999',
                  boxShadow: '0 0 0 1px #d9d9d9 inset',
                }"
            >
                          <a @click="showFileList(record.id)">{{ "" }}</a>
            </a-badge>
            &nbsp;
          </span>

        </div>
      </template>
    </a-table>
    <sub-task-info @subTaskInfo="startProcess" ref="thisSubTaskInfo"></sub-task-info>
    <submitting :title="$t('submitting')" ref="submitting"></submitting>
    <refuse-alert :modal-title="$t('reason')" @confirmFun="stopProcess" ref="refuseAlert"></refuse-alert>
    <show-filelist :detail-id="detailId" ref="showFilelist"></show-filelist>
    <refuse-alert ref="unPassReason" :modal-title="$t('unPass')" @confirmFun="unPassReason"></refuse-alert>
  </div>
</template>

<script>
import util from "@/components/publib/util";
import {formatDate} from "@/components/publib/date";
import Submitting from "@/components/publib/submitting";
import RefuseAlert from "@/components/publib/refuseAlert";
import ShowFilelist from "@/components/publib/showFilelist";
import SubTaskInfo from "@/components/task/processHandle/subTaskInfo";
import {Icon} from "ant-design-vue";

const IconFont = Icon.createFromIconfontCN({
  scriptUrl: util.alicdnIcon,
});

export default {
  name: "processStep2",
  components: {SubTaskInfo, ShowFilelist, RefuseAlert, Submitting, IconFont},
  props: {
    taskId: String,
    process: Object,
    statu: String,
  },
  data: function () {
    this.cacheData = [];
    return {
      tableLoad: false,
      data: [],
      columns: [],
      columnNames: [],
      scroll: {x: 1500, y: 600},
      editingKey: '',
      allUsers: [],
      subs: [],
      selectedRowKeys: [],
      selectedRows: [],
      stopId: "",
      detailId: "",
      subTask: {},
      curFlag: "01",
      subProcessName: "",
      remarks: "",
      subId: "00",
      operators: undefined,
      fileCounts: {}
    }
  },
  beforeMount() {
    this.subId = this.statu;
    this.initPage();
  },
  methods: {
    showFileCount :function (id){
      return this.fileCounts[id];
    },
    sampleInitChange: function (record) {
      record.databasetype = "";
    },
    submitItem: function (record, flag) {
      this.selectedRows = new Array();
      this.selectedRows.push(record);
      this.selectedRowKeys = new Array();
      this.selectedRowKeys.push(record.id);
      this.submitData(flag);
    },
    passItem: function (record, flag) {
      this.selectedRows = new Array();
      this.selectedRows.push(record);
      this.selectedRowKeys = new Array();
      this.selectedRowKeys.push(record.id);
      if (flag) {
        this.submitData("pass");
      } else {
        this.$(this.$refs.unPassReason.$el).modal("show");
      }
    },
    batchUnPass: function () {
      this.$(this.$refs.unPassReason.$el).modal("show");
    },
    unPassReason: function (reason, remark) {
      this.$(this.$refs.unPassReason.$el).modal("hide");
      console.log(reason, remark);
      var postData = {
        subId: this.subId,
        processId: this.process.id,
        data: JSON.stringify(this.selectedRows),
        flag: "unPass",
        subProcessName: this.subProcessName,
        remarks: this.remarks,
        reason: reason,
        remark: remark
      }
      var _this = this;
      // this.$("#submitting").modal("show");
      this.$(this.$refs.submitting.$el).modal("show");
      this.$axios.post("/task/process/unPassMakes", postData).then(function (res) {
        _this.$(_this.$refs.submitting.$el).modal("hide");
        if (res.data.code != "200") {
          _this.$message.error(_this.$t(res.data.code));
        } else {
          _this.$message.success(_this.$t("commitSucc"));
          _this.value = "";
          _this.selectedRowKeys = [];
          _this.selectedRows = [];
          _this.initPage();
          // window.location.reload();
        }
      }).catch(function (res) {
        // _this.$("#submitting").modal("hide");
        _this.$(_this.$refs.submitting.$el).modal("hide");
        console.log(res);
        _this.$message.error(_this.$t("systemErr"));
      })
    },
    deleteByIds: function () {
      var _this = this;
      var postData = {
        ids: this.selectedRowKeys,
        type: "02"
      };
      this.$(this.$refs.submitting.$el).modal("show");
      this.$axios.post("/task/process/deleteByIds", postData).then(function (res) {
        _this.$(_this.$refs.submitting.$el).modal("hide");
        if (res.data.code != "200") {
          _this.$message.error(_this.$t(res.data.code));
        } else {
          _this.$message.success(_this.$t("commitSucc"));
          _this.initPage();
        }
      }).catch(function (res) {
        _this.$(_this.$refs.submitting.$el).modal("hide");
        console.log(res);
        _this.$message.error(_this.$t("systemErr"));
      });
    },
    isDisabled: function (record) {
      if (!this.isEnd) {
        return true;
      }
      if (record.currentstatu == "02"
          || record.currentstatu == "03"
          || record.currentstatu == "09"
          || record.currentstatu == "07"
      ) {
        return true;
      }
      return false;
    },
    handleReset(clearFilters) {
      clearFilters();
      this.searchText = '';
    },
    handleSearch(selectedKeys, confirm, dataIndex) {
      confirm();
      this.searchText = selectedKeys[0];
      this.searchedColumn = dataIndex;
    },
    startProcess: function (subProcessName, remarks) {
      this.$(this.$refs.thisSubTaskInfo.$el).modal("hide");
      this.subProcessName = subProcessName;
      this.remarks = remarks;
      this.submitData("divide");
    },
    subTaskInfo: function () {
      this.$(this.$refs.thisSubTaskInfo.$el).modal("show");
    },
    showSubTask: function (subId) {
      // this.$router.push({name:"processDetail",query:{subId : subId}});
      this.subId = subId;
      this.initPage();
    },
    backToTotal: function () {
      this.$router.push({name: "processInit", query: {taskId: this.process.taskid}});
    },
    isTissu: function (record) {
      if (record.initsample == "02") {
        return true;
      }
      return false;
    },
    handUploadChange: function (ret) {
      console.log(ret);
      if (ret.file.status == "uploading") {
        this.$(this.$refs.submitting.$el).modal("show");
      } else {
        this.$(this.$refs.submitting.$el).modal("hide");
        if (ret.file.status == "error") {
          this.$message.error(this.$t("systemErr"));
        } else if (ret.file.status == "done") {
          this.$message.success(this.$t("commitSucc"));
          this.initPage();
        }
      }
    },
    downLoad: function () {
      var postData = {
        processId: this.process.id,
        makeIds: JSON.stringify(this.selectedRowKeys)
      }
      util.downLoad(postData, "/task/process/downloadMakes", ".xls");
    },
    showFileList: function (detailId) {
      console.log(detailId);
      this.detailId = detailId;
      this.$(this.$refs.showFilelist.$el).modal("show");
    },
    showReason: function (makeId) {
      var _this = this;
      this.$axios.post("/task/process/showStopReason", {detailId: makeId}).then(function (res) {
        if (res.data.code != "200") {
          _this.$message.error(_this.$t(res.data.code));
        } else {
          _this.$error({
            title: _this.$t("reason"),
            content: res.data.retMap.reason
          });
        }
      }).catch(function (res) {
        console.log(res);
        _this.$message.error(_this.$t("systemErr"));
      });
    },
    showStopAlert: function (makeId) {
      this.stopId = makeId;
      this.$(this.$refs.refuseAlert.$el).modal("show");
    },
    stopProcess: function (reason, remark) {
      // this.$("#refuseAlert").modal("hide");
      this.$(this.$refs.refuseAlert.$el).modal("hide");
      var postData = {
        makeId: this.stopId,
        reason: reason,
        remark: remark
      }
      var _this = this;
      this.$(this.$refs.submitting.$el).modal("show");
      this.$axios.post("/task/process/stopMake", postData).then(function (res) {
        _this.$(_this.$refs.submitting.$el).modal("hide");
        if (res.data.code != "200") {
          _this.$message.error(_this.$t(res.data.code));
        } else {
          _this.$message.success(_this.$t("commitSucc"));
          _this.initPage();
        }
      }).catch(function (res) {
        _this.$(_this.$refs.submitting.$el).modal("hide");
        console.log(res);
        _this.$message.error(_this.$t("systemErr"));
      })
    },
    handleUploadChange: function (ret) {
      if (ret.file.status == "done") {
        this.$message.success(this.$t("upload") + this.$t("save_success"));
        this.initPage();
      } else if (ret.file.status == "error") {
        this.$message.error(this.$t("systemErr"));
      }
    },
    colIsDisabed: function (record, col) {
      var sampleType = record.initsample;
      var trans = record.transform;
      if (col == "selfnumber") {
        return true;
      }
      if (
          (
              col == "extractmethod"
              || col == "remaining"
          )
          && sampleType != "02"
      ) {
        return true;
      } else if (
          (
              col == "cellsort"
              || col == "celllife"
          )
          && sampleType != "03" && trans != '03'
      ) {
        return true;
      } else if (
          col == "samplename"
          || col == "selfnumber"
          || col == "species"
          || col == "tissue"
      ) {
        return true;
      }
      return false;
    },
    checkNull: function (list) {
      for (var i = 0; i < list.length; i++) {
        var item = list[i];
        if (util.isNull(item.testdate)) {
          this.$message.error(this.$t("testDate") + this.$t("not_null"));
          return false;
        }
        if (util.isNull(item.concentration)) {
          this.$message.error("(ng/ul)//l)" + this.$t("not_null"));
          return false;
        }
        if (util.isNull(item.concentrationunit)) {
          this.$message.error("" + this.$t("not_null"));
          return false;
        }
        if (util.isNull(item.samplevolume)) {
          this.$message.error(this.$t("sampleVolume") + "(ul)" + this.$t("not_null"));
          return false;
        }
        if (util.isNull(item.totalnumber)) {
          this.$message.error("/ug/" + this.$t("not_null"));
          return false;
        }
        if ((item.initsample == "03" || item.transform == "03") && util.isNull(item.celllife)) {
          this.$message.error(this.$t("cellLife") + this.$t("not_null"));
          return false;
        }
        if (util.isNull(item.checkresult)) {
          this.$message.error(this.$t("checkResult") + this.$t("not_null"));
          return false;
        }
        if (util.isNull(item.checkuser)) {
          this.$message.error(this.$t("checkUser") + this.$t("not_null"));
          return false;
        }
        if (util.isNull(item.reviewer)) {
          this.$message.error(this.$t("reviewer") + this.$t("not_null"));
          return false;
        }
        if (util.isNull(item.databasetype)) {
          this.$message.error(this.$t("databaseType") + this.$t("not_null"));
          return false;
        }
      }
      return true;
    },
    submitData: function (flag) {
      var postData = {
        subId: this.subId,
        processId: this.process.id,
        data: JSON.stringify(this.data),
        flag: flag,
        subProcessName: this.subProcessName,
        remarks: this.remarks
      }
      if (flag != "tmp") {
        postData.data = JSON.stringify(this.selectedRows);
      }
      var _this = this;
      // this.$("#submitting").modal("show");

      if (flag == "complete") {
        if (!this.checkNull(this.selectedRows)) {
          return;
        }
      }
      this.$(this.$refs.submitting.$el).modal("show");
      this.$axios.post("/task/process/submitMakes", postData).then(function (res) {
        console.log(res);
        // _this.$("#submitting").modal("hide");
        _this.$(_this.$refs.submitting.$el).modal("hide");
        if (res.data.code != "200") {
          _this.$message.error(_this.$t(res.data.code));
        } else {
          _this.$message.success(_this.$t("commitSucc"));
          _this.value = "";
          if (flag == "pass") {
            _this.$confirm({
              title: _this.$t("process.passTip") + _this.$t("libraryPreparation") + "?",
              content: _this.$t("process.passContextTip") + _this.$t("libraryPreparation"),
              onOk() {
                _this.submitData("real");
              },
              onCancel() {
                _this.initPage();
              },
              class: 'test',
            });
          } else {
            _this.initPage();
          }
          _this.$emit("changeStatu", "02");
          // window.location.reload();
        }
      }).catch(function (res) {
        // _this.$("#submitting").modal("hide");
        _this.$(_this.$refs.submitting.$el).modal("hide");
        console.log(res);
        _this.$message.error(_this.$t("systemErr"));
      })
    },
    getFileCount: function (ids) {
      var postData = {
        idsStr: JSON.stringify(ids)
      }
      var _this = this;
      this.$axios.post("/file/getFileCount", postData).then(function (res) {
        if (res.data.code == "200") {
          _this.fileCounts = res.data.retMap.fileCounts;
        }
      });
    },
    initPage: function () {
      this.initColumns(this.process.sampletype);
      var _this = this;
      if (util.isNull(this.process.id)) {
        return;
      }
      var postData = {
        processId: this.process.id,
        subId: this.subId,
        curFlag: this.curFlag
      }
      this.tableLoad = true;
      this.$axios.post("/task/process/makeInit", postData).then(function (res) {
        console.log(res);
        _this.tableLoad = false;
        if (res.data.code != "200") {
          _this.$message.error(_this.$t(res.data.code));
        } else {
          _this.data = new Array();
          if (res.data.retMap.makes.length != 0) {
            var ids = new Array();
            for (var ind in res.data.retMap.makes) {
              var make = res.data.retMap.makes[ind];
              make.testdate = formatDate(new Date(make.testdate), "yyyy-MM-dd");
              make.key = make.id;
              _this.data.push(make);
              ids.push(make.id);
            }
            _this.getFileCount(ids);
          }
          _this.cacheData = _this.data.map(item => ({...item}));
          _this.allUsers = res.data.retMap.allUsers;
          // _this.subTask = res.data.retMap.subtask;
          _this.subs = res.data.retMap.subs;
          _this.operators = res.data.retMap.operators;
          _this.selectedRows = [];
          _this.selectedRowKeys = [];
        }
      }).catch(function (res) {
        _this.tableLoad = false;
        console.log(res);
        _this.$message.error(_this.$t("systemErr"));
      })
    },
    initColumns: function (type) {
      console.log(type);
      var initMakesCols = util.initMakesCols();
      this.scroll.x = initMakesCols.scorllLength;
      var clom = initMakesCols.clom;
      this.columns = clom;
      this.columnNames = new Array();
      for (var item in clom) {
        this.columnNames.push(clom[item].dataIndex);
      }
    },
    handleChange(value, key, column) {
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        target[column] = value;
        this.data = newData;
      }
    },
    onDelete(key) {
      const data = [...this.data];
      this.data = data.filter(item => item.key !== key);
    },
    save(key) {
      const newData = [...this.data];
      const newCacheData = [...this.cacheData];
      const target = newData.filter(item => key === item.key)[0];
      if (!this.checkRowData(target)) {
        return;
      }
      target.add = false;
      const targetCache = newCacheData.filter(item => key === item.key)[0];
      if (target && targetCache) {
        this.data = newData;
        Object.assign(targetCache, target);
        this.cacheData = newCacheData;
      }
      target.editable = false;
      this.editingKey = '';
      // console.log(this.data);
    },
    cancel(key) {
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target.add == true) {
        this.editingKey = "";
        this.onDelete(target.key);
        return;
      }
      this.editingKey = '';
      if (target) {
        Object.assign(target, this.cacheData.filter(item => key === item.key)[0]);
        target.editable = false;
        this.data = newData;
      }
    },
    checkRowData: function (rowData) {
      if (util.isNull(rowData.samplename)) {
        this.$message.error(this.$t("sampleName") + this.$t("not_null"));
        return false;
      }
      if (util.isNull(rowData.selfnumber)) {
        this.$message.error(this.$t("sampleIndex") + this.$t("not_null"));
        return false;
      }
      if (util.isNull(rowData.species)) {
        this.$message.error(this.$t("species") + this.$t("not_null"));
        return false;
      }
      if (util.isNull(rowData.tissue)) {
        this.$message.error(this.$t("tissue") + this.$t("animal_stock_resource") + this.$t("not_null"));
        return false;
      }
      if (util.isNull(rowData.testdate)) {
        util.$message.error(this.$t("testDate") + this.$t("not_null"));
        return false;
      }
      if (rowData.concentration < 0) {
        this.$message.error(this.$t("concentration") + this.$t("not_null"));
        return false;
      }
      if (rowData.samplevolume < 0) {
        this.$message.error(this.$t("sampleVolume") + this.$t("not_null"));
        return false;
      }
      if (this.process.sampletype == "02" && util.isNull(rowData.remaining)) {
        this.$message.error(this.$t("remaining") + this.$t("not_null"));
        return false;
      }
      if (util.isNull(rowData.checkresult)) {
        this.$message.error(this.$t("checkResult") + this.$t("not_null"));
        return false;
      }
      if (this.process.sampletype == "02" && util.isNull(rowData.extractmethod)) {
        this.$message.error(this.$t("extractMethod") + this.$t("not_null"));
        return false;
      }
      if (rowData.totalNumber < 0) {
        this.$message.error(this.$t("totalNumber") + this.$t("not_null"));
        return false;
      }
      if (this.process.sampletype == "03" && util.isNull(rowData.celllife)) {
        this.$message.error(this.$t("celllife") + this.$t("not_null"));
        return false;
      }
      if (this.process.sampletype == "03" && util.isNull(rowData.cellsort)) {
        this.$message.error(this.$t("cellsort") + this.$t("not_null"));
        return false;
      }
      if (util.isNull(rowData.databasetype)) {
        this.$message.error(this.$t("databaseType") + this.$t("not_null"));
        return false;
      }
      if (util.isNull(rowData.sequencingplatform)) {
        this.$message.error(this.$t("SequencingPlatform") + this.$t("not_null"));
        return false;
      }
      return true;
    },
    databaseTypes: function (type, trans) {
      if (util.isNull(trans)) {
        return util.databaseTypes(type);
      } else {
        return util.databaseTypes(trans);
      }
    },
    isStop: function (record) {
      if (record.currentstatu == "09"
          || record.currentstatu == "07"
      ) {
        return true;
      }
      return false;
    },
    showText: function (clo, text, ind) {
      if (clo == "samplemsg") {
        for (var index in this.sampletypes) {
          var item = this.sampletypes[index];
          if (item.key == text) {
            return item.val;
          }
        }
      }

      if (clo == "samplestatu") {
        for (var statuIndex in this.sampleStatu) {
          var statu = this.sampleStatu[statuIndex];
          if (statu.key == text) {
            return statu.val;
          }
        }
      }

      if (clo == "cellsort") {
        for (var cellSortIndex in this.cellSortMethods) {
          var cellSort = this.cellSortMethods[cellSortIndex];
          if (cellSort.key == text) {
            return cellSort.val;
          }
        }
      }

      if (clo == "databasetype") {
        for (var databaseTypeIndex in this.databaseTypes) {
          var databaseType = this.databaseTypes[databaseTypeIndex];
          if (databaseType.key == text) {
            return databaseType.val;
          }
        }
      }

      if (clo == "sequencingplatform") {
        for (var seqPlantsIndex in this.seqPlants) {
          var seqPlant = this.seqPlants[seqPlantsIndex];
          if (seqPlant.key == text) {
            return seqPlant.val;
          }
        }
      }

      if (clo == "checkuser" || clo == "reviewer") {
        for (var userIndex in this.allUsers) {
          var user = this.allUsers[userIndex];
          if (user.id == text) {
            return user.name;
          }
        }
      }

      if (clo == "extractmethod") {
        for (var extractIndex in this.extractmethods) {
          var extract = this.extractmethods[extractIndex];
          if (extract.key == text) {
            return extract.val;
          }
        }
      }

      if (clo == "checkresult") {
        for (var checkresultIndex in this.checkresults) {
          var checkresult = this.checkresults[checkresultIndex];
          if (checkresult.key == text) {
            return checkresult.val;
          }
        }
      }

      if (clo == "testdate") {
        if (util.isNull(text)) {
          return "";
        }
        return formatDate(new Date(text), "yyyy-MM-dd");
      }

      if (clo == "index") {
        return ind + 1;
      }
      return text;
    },

    uploadHeader: function (record) {
      return {token: this.$cookies.get('token'), make: record.id}
    },
  },
  computed: {
    allUploadHeader : function (){
      return {token: this.$cookies.get('token'), idsStr: JSON.stringify(this.selectedRowKeys)}
    },
    rowSelection() {
      const {selectedRowKeys, selectedRows} = this;
      return {
        selectedRowKeys: selectedRowKeys,
        selectedRows: selectedRows,
        onChange: (selectedRowKeys, selectedRows) => {
          console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
          this.selectedRowKeys = selectedRowKeys;
          this.selectedRows = selectedRows;
        },
        getCheckboxProps: record => ({
          props: {
            // Column configuration not to be checked
            disabled: record.currentstatu == "07"
                || record.currentstatu == "09",
          },
        }),
      };
    },
    concentrationName: function () {
      if (this.process.sampletype == "01") {
        return this.$t("concentration") + "(ng/ul)";
      } else {
        return this.$t("concentration") + "(" + this.$t("cellNumber") + "/l)";
      }
    },
    totalNumberTitle: function () {
      if (this.process.sampletype == "01") {
        return this.$t("nucleicAcid") + this.$t("totalNumber") + "(ug)";
      } else {
        return this.$t("cell") + this.$t("totalNumber") + "(" + this.$t("cellNumber") + ")";
      }
    },
    cellSortMethods: function () {
      return util.cellSortMethods();
    },
    seqPlants: function () {
      return util.seqPlants();
    },
    extractmethods: function () {
      return util.extractmethods();
    },
    checkresults: function () {
      return util.checkresults();
    },
    sampleInits: function () {
      return util.sampleInits();
    },
    transformSamples: function () {
      var arr = new Array();
      for (var i = 0; i < this.sampleInits.length; i++) {
        // if (this.sampleInits[i].key != "02"){
        //   arr.push(this.sampleInits[i]);
        // }
        arr.push(this.sampleInits[i]);
      }
      return arr;
    },
    canOperating: function () {
      if (this.process.taskstatu != "20") {
        return false;
      }
      if (this.$store.getters.getUser.id != this.process.samplepreparation) {
        return false;
      }
      return true;
    },
    canEdit: function () {
      if (
          !this.$store.getters.isCurrentUser(this.process.samplepreparation)
          && !this.$store.getters.isAdmin
      ) {
        return false;
      }
      return true;
    },
    cansubmit() {
      if (this.selectedRowKeys.length == 0) {
        return true;
      }
      for (var item in this.selectedRows) {
        if (this.selectedRows[item].currentstatu != '03') {
          return true;
        }
      }
      return false;
    },
    canDivide() {
      if (this.selectedRowKeys.length == 0) {
        return true;
      }
      for (var item in this.selectedRows) {
        if (!util.isNull(this.selectedRows[item].subid)
            || this.selectedRows[item].currentstatu == "02" || this.selectedRows[item].currentstatu == '03'
        ) {
          return true;
        }
      }
      return false;
    },
    canComplete() {
      if (this.selectedRowKeys.length == 0) {
        return true;
      }
      for (var item in this.selectedRows) {
        if (this.selectedRows[item].currentstatu == '02'
            || this.selectedRows[item].currentstatu == '03'
            || this.selectedRows[item].currentstatu == '09'
            || this.selectedRows[item].currentstatu == '07'
        ) {
          return true;
        }
      }
      return false;
    },
    uploadAllUrl: function () {
      return this.$axios.defaults.baseURL + 'file/import/uploadAll';
    },
    uploadUrl: function () {
      return this.$axios.defaults.baseURL + 'file/import/makeUpload';
    },
    isEnd: function () {
      if (!this.process.taskstatu.startsWith("7")
          && this.$store.getters.isCurrentUser(this.process.samplepreparation)
      ) {
        return true;
      }
      return false;
    },
    canPase: function () {
      if (!this.process.taskstatu.startsWith("7")
          && this.$store.getters.isCurrentUser(this.process.creater)
      ) {
        return true
      }
      return false;
    },
    disabledPass() {
      if (this.selectedRowKeys.length == 0) {
        return true;
      }
      for (var item in this.selectedRows) {
        if (this.selectedRows[item].currentstatu != '02') {
          return true;
        }
      }
      return false;
    },
    disabledUpload() {
      if (this.selectedRowKeys.length == 0) {
        return true;
      }
      for (var item in this.selectedRows) {
        if (this.selectedRows[item].currentstatu != '01') {
          return true;
        }
      }
      return false;
    },
    createrName: function () {
      if (this.operators === undefined) {
        return '';
      }
      return this.operators.creater.name;
    },
    operatorName: function () {
      if (this.operators === undefined) {
        return '';
      }
      return this.operators.make.name;
    }
  },
  watch: {
    process: {
      handler: function () {
        // console.log(newVal)
        this.initPage();
      },
      deep: true
    },
    statu(newVal) {
      this.subId = newVal;
      this.initPage();
    }
  }
}
</script>

