<template>
  <div style="margin-bottom: 20px">
    <a-page-header
        style="border: 1px solid rgb(235, 237, 240)"
        :title="$t('diyStatu')"
        :sub-title="process.projectname"
    >
      <template slot="extra">
        <a-button type="primary" @click="addNewRow">
          {{$t("add")}}
        </a-button>
      </template>
    </a-page-header>
    <a-table :columns="columns"
             :data-source="samples"
             bordered
             :scroll="scroll"
             :loading="tableLoad"
             :pagination="false"
             size="small">
      <div
          slot="filterDropdown"
          slot-scope="{ setSelectedKeys, selectedKeys, confirm, clearFilters, column }"
          style="padding: 8px"
      >
        <a-input
            v-ant-ref="c => (searchInput = c)"
            :value="selectedKeys[0]"
            style="width: 188px; margin-bottom: 8px; display: block;"
            @change="e => setSelectedKeys(e.target.value ? [e.target.value] : [])"
            @pressEnter="() => handleSearch(selectedKeys, confirm, column.dataIndex)"
        />
        <a-button
            type="primary"
            icon="search"
            size="small"
            style="width: 90px; margin-right: 8px"
            @click="() => handleSearch(selectedKeys, confirm, column.dataIndex)"
        >
          {{ $t("search") }}
        </a-button>
        <a-button size="small" style="width: 90px" @click="() => handleReset(clearFilters)">
          {{ $t("reset") }}
        </a-button>
      </div>
      <a-icon
          slot="filterIcon"
          slot-scope="filtered"
          type="search"
          :style="{ color: filtered ? '#108ee9' : undefined }"
      />
      <template
          v-for="col in this.columnNames"
          :slot="col"
          slot-scope="text, record"
      >
        <div :key="col">
          <a-input
              v-if="col == 'diyname' || col == 'diystatu'"
              style="width: 100%"
              v-model="record[col]"
          />
          <a-select
              mode="multiple"
              placeholder="Please select"
              style="width: 100%"
              v-if="col == 'inputs'"
              v-model="record.inputs"
          >
            <a-select-option v-for="item in inputs" :key="item.id">
              {{ item.samplename + "-" + item.species + "-" + item.tissue}}
            </a-select-option>
          </a-select>
        </div>
      </template>
      <template slot="operation" slot-scope="text, record">
        <a-popconfirm
            title="Sure to delete?"
            @confirm="() => onDelete(record)"
        >
          <a href="javascript:;">Delete</a>
        </a-popconfirm>
        &nbsp;
        <a  @click="saveDiyStatu(record)">Save</a>
      </template>
    </a-table>
    <submitting :title="$t('submitting')" ref="submitting"></submitting>
  </div>
</template>

<script>
import util from "@/components/publib/util";
import Submitting from "@/components/publib/submitting";

export default {
  name: "ProcessDiyStatu",
  components: {Submitting},
  props: {
    process: Object
  },
  beforeMount() {
    this.initPage();
  },
  data: function () {
    return {
      samples: [],
      tableLoad: false,
      searchText: '',
      scroll: {y: 500},
      searchedColumn: '',
      columns: [],
      columnNames: [],
      inputs : []
    }
  },
  methods: {
    saveDiyStatu : function (record){
      var postData = {
        diyStatu : JSON.stringify(record)
      }
      var _this;
      util.commonPost("/task/process/saveDiyStatu",postData,function (){
        _this.initPage();
      },this.$refs.submitting.$el);
    },
    onDelete : function (record){
      if (record.key != record.id){
        const dataSource = [...this.samples];
        this.samples = dataSource.filter(item => item.key !== record.key);
      }else {
        util.commonPost("/task/process/deleteDiyStatu",
            {id : record.id},function (){},this.$refs.submitting.$el);
      }
    },
    addNewRow : function (){
      var key = new Date().getDate();
      var newRow = {
        key : key,
        diyname : "",
        diystatu : "",
        inputs : new Array(),
        processid : this.process.id
      }
      this.samples.push(newRow);
    },
    initPage: function () {
      this.initColumns();
      var postData = {
        processId : this.process.id
      }
      var _this = this;
      this.tableLoad = true;
      this.$axios.post("/task/process/diyStatuInit",postData).then(function (res){
        _this.tableLoad = false;
        if (res.data.code != 200) {
          _this.$message.error(_this.$t(res.data.code));
        } else {
          console.log(res);
          _this.samples = new Array();
          if (res.data.retMap.diys != undefined && res.data.retMap.diys.length > 0){
            for (var i = 0; i <  res.data.retMap.diys.length; i++){
              var item = res.data.retMap.diys[i];
              item.key = item.id;
              _this.samples.push(item);
            }
          }
          _this.inputs = res.data.retMap.inputs;
        }
      }).catch(function (res){
        _this.tableLoad = false;
        console.log(res);
        _this.$message.error(_this.$t("systemErr"));
      })
    },
    handleSearch(selectedKeys, confirm, dataIndex) {
      confirm();
      this.searchText = selectedKeys[0];
      this.searchedColumn = dataIndex;
    },
    handleReset(clearFilters) {
      clearFilters();
      this.searchText = '';
    },
    initColumns: function () {
      var clom = new Array();
      /*  */
      clom.push({
        title: this.$t("diySampleName"),
        dataIndex: 'diyname',
        key: 'diyname',
        scopedSlots: {
          filterDropdown: 'filterDropdown',
          filterIcon: 'filterIcon',
          customRender: 'diyname',
        },
        onFilter: (value, record) =>
            record.diyname
                .toString()
                .toLowerCase()
                .includes(value.toLowerCase()),
        onFilterDropdownVisibleChange: visible => {
          if (visible) {
            setTimeout(() => {
              this.searchInput.focus();
            }, 0);
          }
        },
      });

      clom.push({
        title: this.$t("diyStatu"),
        dataIndex: 'diystatu',
        key: 'diystatu',
        scopedSlots: {
          filterDropdown: 'filterDropdown',
          filterIcon: 'filterIcon',
          customRender: 'diystatu',
        },
        onFilter: (value, record) =>
            record.diystatu
                .toString()
                .toLowerCase()
                .includes(value.toLowerCase()),
        onFilterDropdownVisibleChange: visible => {
          if (visible) {
            setTimeout(() => {
              this.searchInput.focus();
            }, 0);
          }
        },
      });

      // 
      clom.push({
        title: this.$t("connect") + this.$t("sampleName"),
        dataIndex: 'inputs',
        key: 'inputs',
        scopedSlots: {
          filterDropdown: 'filterDropdown',
          filterIcon: 'filterIcon',
          customRender: 'inputs',
        },
        onFilter: (value, record) =>
            record.inputs
                .toString()
                .toLowerCase()
                .includes(value.toLowerCase()),
        onFilterDropdownVisibleChange: visible => {
          if (visible) {
            setTimeout(() => {
              this.searchInput.focus();
            }, 0);
          }
        },
      });

      /***/
      clom.push({
        slots: { title: 'operationTitle' },
        // title: i18n.t("operation"),
        dataIndex: 'operation',
        width: '150px',
        fixed: 'right',
        scopedSlots: {customRender: 'operation'},
      });

      this.columns = clom;
      this.columnNames = new Array();
      for (var item in clom) {
        this.columnNames.push(clom[item].dataIndex);
      }
    }
  },
  watch: {
    process: {
      handler: function () {
        this.initPage();
      },
      deep: true
    }
  }
}
</script>

<style scoped>

